import React from 'react'
import { Link } from 'gatsby'
import { GatsbyImage } from "gatsby-plugin-image";

const ProjectItem = ({ project }) => {
    const { description, title, frontimage, date, status } = project.frontmatter
    const { slug } = project.fields
    return (
        <Link className="projects-item__link" to={`/projects${slug}`}>
            <div className="projects-item">            
                <div className="projects-item__img-wrapper">
                    { frontimage ? <GatsbyImage image={frontimage.childImageSharp?.gatsbyImageData} alt="" className="projects-item__img" /> : '' }
                </div>            
                <div className="projects-item__content">
                    <h3 className="projects-item__title">
                        {title}
                    </h3>
                    <p className="projects-item__footer">{status}</p>
                </div>            
            </div>
        </Link>
    );

}

export default ProjectItem