import React from "react"
import { graphql } from 'gatsby'

import Layout from "/src/layouts/main/layout/layout.js"
import Seo from "../components/seo"
import ProjectList from '../components/ProjectList/ProjectList'


const ProjectsPage = ({ data }) => {
  
  return (
    <Layout>
      <Seo title="Projects" />
      {<ProjectList projects={ data.allMarkdownRemark.nodes } />}
    </Layout>
  )
}

export default ProjectsPage

export const pageQuery = graphql`query ProjectsQuery {
    allMarkdownRemark(
      sort: {fields: [frontmatter___date], order: DESC}
      limit: 1000
      filter: {fields: {collection: {eq: "projects"}}, frontmatter: {visibility: {eq: "public"}}}
    ) {
      nodes {
        id
        fields {
          slug
        }
        frontmatter {
          title
          description
          start_date(formatString: "MMMM DD, YYYY", locale: "es")
          status
          frontimage {
            childImageSharp {
              gatsbyImageData(layout: CONSTRAINED, transformOptions: {}, width: 400)
            }
          }
        }
      }
    }
  }
  
`